import { createRouter, createWebHistory } from 'vue-router'
import { useUserStore } from '../stores/user'

const routes = [
  {
    path: '/',
    name: 'Layout',
    component: () => import('../views/Layout.vue'),
    redirect: '/dashboard',
    meta: { requiresAuth: true },
    children: [
      {
        path: 'dashboard',
        name: 'Dashboard',
        component: () => import('../views/Dashboard.vue'),
        meta: { title: '仪表盘' }
      },
      {
        path: 'shops',
        name: 'Shops',
        component: () => import('../views/Shops.vue'),
        meta: { title: '店铺管理' }
      },
      {
        path: 'images',
        name: 'Images',
        component: () => import('../views/Images.vue'),
        meta: { title: '图片管理' }
      },
      {
        path: 'settings',
        name: 'Settings',
        component: () => import('../views/Settings.vue'),
        meta: { title: '设置' }
      },
      {
        path: 'keywords',
        name: 'KeywordsManagement',
        component: () => import('../views/KeywordsManagement.vue'),
        meta: { title: '关键词管理' }
      },
      {
        path: 'product-publish',
        name: 'ProductPublish',
        component: () => import('../views/ProductPublish.vue'),
        meta: { title: '商品发布' }
      },
      {
        path: 'publish-tasks',
        name: 'PublishTaskPool',
        component: () => import('../views/PublishTaskPool.vue'),
        meta: { title: '发布任务池' }
      },
      {
        path: 'description-tester',
        name: 'DescriptionTester',
        component: () => import('../views/DescriptionTester.vue'),
        meta: { title: '描述生成测试' }
      },
      {
        path: 'coze-test',
        name: 'CozeTest',
        component: () => import('../views/CozeTestPage.vue'),
        meta: { title: 'Coze工作流测试' }
      }
    ]
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login.vue'),
    meta: { title: '登录' }
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import('../views/Register.vue'),
    meta: { title: '注册' }
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    component: () => import('../views/NotFound.vue'),
    meta: { title: '页面不存在' }
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

// 导航守卫
router.beforeEach((to, from, next) => {
  // 设置页面标题
  document.title = `${to.meta.title || '首页'} - 闲鱼商品发布助手`
  
  // 获取用户状态
  const userStore = useUserStore()
  
  // 如果用户未登录且不是前往登录或注册页面，直接重定向到登录页
  if (!userStore.token && to.name !== 'Login' && to.name !== 'Register') {
    next({ name: 'Login', query: { redirect: to.fullPath !== '/' ? to.fullPath : undefined } })
  } 
  // 已登录用户访问登录/注册页，重定向到仪表盘
  else if (userStore.token && (to.name === 'Login' || to.name === 'Register')) {
    next({ name: 'Dashboard' })
  } 
  // 其他情况正常访问
  else {
    next()
  }
})

export default router 